import Browser from 'webextension-polyfill';

(() => {
  if (document !== null) {
    document
      .querySelector('button#requestPermissions')
      ?.addEventListener('click', async () => {
        const response = await Browser.permissions.request({
          origins: ['*://*.toggl.com/*', '*://toggl.com/*'],
        });

        if (response) {
          const url = Browser.runtime.getURL(
            'src/pages/postInstallLP/index.html'
          );

          void Browser.tabs.update({ url });
        }
      });
  }
})();
